import React, { Component } from 'react';
import './Game.css';
import BlockiesIdenticon from './BlockiesIdenticon';


class CardView extends Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    if (!this.props.matched && !this.props.imageUp) {
      this.props.onClick(this.props.id,this.props.image);      
    }
  }

  render() {
    let className='Card';
    if (this.props.matched) {
      className = className + ' Matched';
    }

    let opts = {
      seed: this.props.value,
      size: 10,
      scale: 8
    };

    var contents;
    
    if (this.props.imageUp) {
      contents = (
          <BlockiesIdenticon opts={opts} onClick={this.onClick}/>
      );
    } else {
      let imPath = './images/back.jpg';
      contents = (
        <img src={require(`${imPath}`)} alt='' onClick={this.onClick}/>
      );
    }

    return (
        <div className={className}>{contents}</div>
    );
  };
};

export default CardView;



